/* RESETS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

/* DEFAULTS */
html {
  font-size: 62.5%;
}
body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 2.5rem;
  color: #555555;
  background-color: #222222;
}
.app {
  padding: 80px 0;
  max-width: 800px;
  min-width: 640px;
  margin: 0 auto;
}
h1 {
  font-size: 4rem;
  color: #fff;
  text-align: center;
  margin-bottom: 50px;
  text-transform: uppercase;
}

@media screen and (max-width: 768px) {
  .app {
    padding: 80px 20px;
    width: 100%;
    min-width: 100%;
  }
  h1 {
    font-size: 2.2rem;
    margin-bottom: 30px;
  }
  body {
    font-size: 1.5rem;
  }
}
.alert {
  padding: 15px;
  font-size: 1.8rem;
  line-height: 2.2rem;
  margin-top: 20px;
  color: #fff;
  border-radius: 3px;
}
.alert__error {
  background-color: #FF464B;
}

@media screen and (max-width: 768px) {
  .alert {
    margin-top: 15px;
    padding: 10px;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}
.btn {
  height: 80px;
  border-radius: 3px;
  font-size: 2.8rem;
  font-weight: 700;
  color: #fff;
  padding: 15px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  display: block;
  width: 100%;
}
.btn__success {
  background-color: #28BF7B;
}
.btn__success:hover,
.btn__success:focus {
  background-color: #1D8C5A;
}
.btn__danger {
  background-color: #D64B4B;
}
.btn__danger:hover,
.btn__danger:focus {
  background-color: #A33939;
}

@media screen and (max-width: 768px) {
  .btn {
    height: 45px;
    font-size: 1.5rem;
  }
}
.form__field {
  background-color: #fff;
  border: none;
  height: 80px;
  border-radius: 3px;
  padding: 10px 20px;
  font-size: 2.8rem;
  font-weight: 400;
  color: hsl(0, 0%, 33%);
  width: 100%;
}
.form__field::-webkit-input-placeholder {
  color: #CCCCCC;
}
.form__field::-ms-input-placeholder {
  color: #CCCCCC;
}
.form__field::placeholder {
  color: #CCCCCC;
}
.form__field:-ms-input-placeholder {
  color: #CCCCCC;
}
.form__field::-ms-input-placeholder {
  color: #CCCCCC;
}
.map__location {
  display: block;
  margin: 0 auto 60px;
}

@media screen and (max-width: 768px) {
  .map__location {
    margin-bottom: 40px;
    height: 100px;
  }
}
.location {
  margin: 60px 0;
  background-color: #fff;
  padding: 30px;
  border-radius: 3px;
}
.location__item>strong {
  margin-right: 8px;
  font-weight: 700;
}
.location__item {
  font-weight: 400;
  margin-bottom: 10px;
}
.location__item:last-child {
  margin-bottom: 0;
}

@media screen and (max-width: 768px) {
  .location {
    margin: 30px 0;
    padding: 20px;
  }
}
.form-zipcode.inline>.form__group {
  display: inline-block;
  width: 62%;
}
.form-zipcode.inline>.form__group:last-child {
  width: 38%;
}
.form-zipcode.inline .form__field {
  border-radius: 3px 0 0 3px;
}
.form-zipcode.inline>.form__group:last-child>.btn {
  border-radius: 0 3px 3px 0;
}

@media screen and (max-width: 768px) {
  .form-zipcode.inline>.form__group:last-child,
  .form-zipcode.inline>.form__group {
    width: 100%;
    display: block;
  }
  .form-zipcode.inline .form__field {
    border-radius: 3px 3px 0 0;
    height: 50px;
    font-size: 1.5rem;
    padding: 12px;
  }
  .form-zipcode.inline>.form__group:last-child>.btn {
    border-radius: 0 0 3px 3px;
  }
}
